import React from "react";
import {
    Card,
    CardContent,
    makeStyles,
    Typography,
    Grid
} from '@material-ui/core';
import kakeibo from '../img/kakeibo.png';
import portforio from '../img/portforio.png';

const useStyle = makeStyles(() => ({
    body: {
        background: 'white',
        paddingTop: '30px',
        paddingBottom: '30px',
        paddingLeft: '35px',
    },
    card: {
        border: '3px solid black',
        background: 'white',
        borderRadius: '8px',
        width: '400px',
        height: '700px',
    },
    grid: {
        margin: "auto",
    },
    img: {
        height: "300px",
        width: '400px',
        objectFit: 'cover',
        textAlign: 'center',
    },
    title: {
        textAlign: 'center',
    },
    line: {
        border: '1px solid black',
    }
}))

const ProductCard = (props) => {
    const { title, img,alt, comment, skill, reflection } = props;
    const classes = useStyle();

    return (
        <Card variant="outlined" className={classes.card} alignItems="center" justify="center">
            <img className={classes.img} src={img} alt={alt}/>
            <CardContent>
                <Typography className={classes.title}>{title}</Typography>
                <hr className={classes.line}></hr>
                <br></br>
                <Typography variant="body2" className={classes.explation}>{comment}</Typography>
                <br></br>
                <Typography variant="body2" className={classes.explation}>
                    :
                    <br></br>
                    {skill}
                </Typography>
                <br></br>
                <Typography variant="body2" className={classes.explation}>
                    :
                    <br></br>
                    {reflection}
                </Typography>
            </CardContent>
        </Card>
    );
}

const products = [
    {
        title: "LINE bot",
        img: kakeibo,
        alt:"",
        comment: "LINE",
        skill: "Python(Flask)line-bot-sdk-pythonGCP(SpreadSheet)Heroku",
        reflection: "",
    },
    {
        title: "",
        img: portforio,
        alt:"",
        comment: "ReactProductSkill",
        skill: "JavaScript(React)material-uireact-router-domreact-slider-domreact-ityped",
        reflection: "CSSBlog",
    },
];

const Products = ()=> {
    const classes = useStyle();

    const getCardContent = (getObj) => {
        return (
            <Grid item xs={12} sm={8} md={6} className={classes.grid}>
                <ProductCard {...getObj} />
            </Grid>
        );
    };

    return (
        <div className={classes.body}>
            <Grid container spacing={4} alignItems="center">
                {products.map((contentObj) => getCardContent(contentObj))}
            </Grid>
        </div>
    );
}

export default Products;
